.nav {
  &__list {
    display: flex;
    justify-content: space-between;
    padding-top: 5%;
  }

  &__link {
    color: #fff;
    font-size: 12px;
    &:hover {
      color: #9fadc2;
    }

    &--active {
      padding: 8px 24px;
      border-radius: 8px;
      color: #fff;
      background-color: rgba( 255, 255, 255, 0.1);
    }
  }
}

@media screen and (max-width:800px) {
  .nav {
    display: none;
  }
}

@media screen and (min-width:1900px) {
  .nav__list {
    margin: -3% 10%;
  }
}
